<template>
    <div class="memolist">
        <h1>Memo</h1>
        <table>
            <tr>
                <td>No.</td>
                <td>Title</td>
                <td>Writer</td>
            </tr>
            <tr>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            memo : []
        }
    },
    created() {
        this.$http.get('/api/memo')
            .then((res) =>{
                this.memo = res.data;
            })
    }
}
</script>